'use client';

import { useState, useEffect } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/Card';
import { DataTable } from '@/components/DataTable';
import { Badge } from '@/components/ui/Badge';
import { Button } from '@/components/ui/Button';
import { DashboardCard } from '@/components/DashboardCard';
import { ChartCard } from '@/components/ChartCard';
import { DashboardSkeleton } from '@/components/loading/Skeletons';
import { AnimatedCounter } from '@/components/animations/AnimatedCounter';
import { TrendingUp, Download } from 'lucide-react';
import { PieChart, Pie, Cell, Legend, Tooltip, ResponsiveContainer, BarChart, Bar, XAxis, YAxis, CartesianGrid } from 'recharts';
import { mockCriteriaBreakdown } from '@/data/mock';

const sentimentData = [
  { name: 'Positive', value: 65, fill: '#10B981' },
  { name: 'Neutral', value: 25, fill: '#F59E0B' },
  { name: 'Negative', value: 10, fill: '#EF4444' },
];

export default function TeacherResults() {
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const timer = setTimeout(() => setIsLoading(false), 800);
    return () => clearTimeout(timer);
  }, []);

  if (isLoading) return <DashboardSkeleton />;

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-3xl font-bold text-gray-900 dark:text-white">Evaluation Results</h1>
        <p className="text-gray-600 dark:text-gray-400 mt-2">
          View your comprehensive evaluation results and analytics
        </p>
      </div>

      {/* Key Metrics */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <DashboardCard
          title="Overall Rating"
          value={
            <span>
              <AnimatedCounter endValue={46} decimals={1} suffix="/50" />
            </span>
          }
          footer="Based on 145 responses"
          icon={<TrendingUp className="w-6 h-6" />}
          color="green"
        />
        <DashboardCard
          title="Completion Rate"
          value={<AnimatedCounter endValue={89} suffix="%" />}
          footer="Students evaluated"
          icon={<TrendingUp className="w-6 h-6" />}
          color="blue"
        />
        <DashboardCard
          title="Department Rank"
          value="#2"
          footer="Out of 15 instructors"
          icon={<TrendingUp className="w-6 h-6" />}
          color="yellow"
        />
      </div>

      {/* Charts */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Criteria Scores */}
        <ChartCard
          title="Criteria Performance"
          description="Your average score by criteria"
        >
          <ResponsiveContainer width="100%" height={250}>
            <BarChart data={mockCriteriaBreakdown}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="criteriaName" />
              <YAxis domain={[0, 5]} />
              <Tooltip />
              <Bar dataKey="score" fill="#2563EB" />
            </BarChart>
          </ResponsiveContainer>
        </ChartCard>

        {/* Sentiment Distribution */}
        <ChartCard
          title="Feedback Sentiment"
          description="Distribution of positive, neutral, and negative comments"
        >
          <ResponsiveContainer width="100%" height={250}>
            <PieChart>
              <Pie
                data={sentimentData}
                cx="50%"
                cy="50%"
                labelLine={false}
                label={({ name, percent }) => `${name} ${(percent * 100).toFixed(0)}%`}
                outerRadius={80}
                fill="#8884d8"
                dataKey="value"
              >
                {sentimentData.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={entry.fill} />
                ))}
              </Pie>
              <Tooltip />
            </PieChart>
          </ResponsiveContainer>
        </ChartCard>
      </div>

      {/* Detailed Results by Course */}
      <Card>
        <CardHeader>
          <CardTitle>Results by Course</CardTitle>
          <CardDescription>Breakdown of evaluation results for each course</CardDescription>
        </CardHeader>
        <CardContent>
          <DataTable
            columns={[
              {
                key: 'id' as any,
                label: 'Course',
                render: () => 'IT101 - Intro to Computing',
              },
              {
                key: 'id' as any,
                label: 'Section',
                render: () => 'A',
              },
              {
                key: 'id' as any,
                label: 'Responses',
                render: () => '45',
              },
              {
                key: 'id' as any,
                label: 'Average Score',
                render: () => (
                  <span className="font-semibold text-green-600 dark:text-green-400">
                    4.8/5.0
                  </span>
                ),
              },
              {
                key: 'id' as any,
                label: 'Status',
                render: () => <Badge variant="success">Completed</Badge>,
              },
              {
                key: 'id' as any,
                label: 'Action',
                render: () => (
                  <Button variant="outline" size="sm">
                    View Report
                  </Button>
                ),
              },
            ]}
            data={[{ id: 1 }, { id: 2 }, { id: 3 }]}
          />
        </CardContent>
      </Card>

      {/* Export Section */}
      <Card>
        <CardHeader>
          <CardTitle>Export Results</CardTitle>
          <CardDescription>Download your evaluation results in various formats</CardDescription>
        </CardHeader>
        <CardContent className="flex gap-3">
          <Button variant="outline" size="lg">
            <Download className="w-4 h-4 mr-2" />
            PDF Report
          </Button>
          <Button variant="outline" size="lg">
            <Download className="w-4 h-4 mr-2" />
            Excel
          </Button>
        </CardContent>
      </Card>
    </div>
  );
}
